@font-face {
  font-family: 'DIN Pro';
  src: url('../fonts/DinPro/DINPro-Medium.eot');
  src: local("../fonts/DinPro/DIN Pro Medium"), local("../fonts/DinPro/DINPro-Medium"),
  url('../fonts/DinPro/DINPro-Medium.eot?#iefix') format("embedded-opentype"),
  url('../fonts/DinPro/DINPro-Medium.woff') format("woff"), url('../fonts/DinPro/DINPro-Medium.ttf') format("truetype") {}
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'DIN Pro Cond';
